}

.toggle-button {
  border: none;
  background: none;
  color: white;
  font-size: 20px; /* Adjust as needed */
  cursor: pointer;
}

.toggle-button.down::before {
  content: "▼"; /* Arrow down */
}

.toggle-button.up::before {
  content: "▲"; /* Arrow up */
}

.answer {
  color: #FFF;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
